import { CONSTANTS } from '../../actions/login'

let initialState = {
    formData: {
        'username': '',
        'password': ''
    },
    currentlySending: false,
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        default:
            return state
    }
}

export default reducer


